// logo
.logo {
    width: 96px;
    height: 24px;
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week8/logo-aloha.svg");
    background-position: center center;
    background-size: auto;
    background-repeat: no-repeat;
    a {
        width: 100%;
        height: 100%;
        display: block;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
    }
}
